/* Signup View Container */
.signup-container {
    padding: 2rem;
    border-radius: 8px;
    color: #ffffff; /* White text color for readability */
    width: 100%;
    max-width: 500px;
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    box-shadow: 0 0 8px 0;
    background: inherit;
    backdrop-filter: blur(10px);
}

/* General Form Styling */
.form-group {
    margin-bottom: 1.5rem;
    width: 100%;  /* Ensures the form group takes full width */
}

.form-label {
    display: block;  /* Ensures the label is on a new line */
    font-weight: bold;
    margin-bottom: 0.5rem; /* Space between label and input */
    color: #ffffff; /* Keeps label text white for readability */
}

.form-control {
    display: block;  /* Ensures the input is on a new line */
    width: 100%;  /* Full width to match label */
    padding: 0.75rem;
    border: 1px solid #444; /* Darker border for contrast */
    border-radius: 4px;
    background-color: transparent; /* Transparent background for inputs */
    color: #ffffff; /* White input text for visibility */
    font-size: 1rem;
}

.form-control:focus {
    border-color: #007bff; /* Blue border on focus */
    box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25); /* Subtle glow on focus */
}

/* Button Styling to Match Profile Button */
.button {
    padding: 10px 20px;
    background-color: #E50914; /* Netflix red for buttons */
    color: white;
    border: none;
    border-radius: 4px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s;
    width: 100%;  /* Full width for consistency with input fields */
    margin-top: 1rem; /* Space above the button */

    &:hover {
        background-color: #f22030; /* Slightly darker red on hover */
    }
}
